@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700&display=swap');

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Poppins', sans-serif;
}

h1 {
    color: black;
    text-align: center;
}

.main-container {
    color: black;
    background-color: #4f1919;
    height: 100vh;
    width: 100vw;
    border-radius: 0;
    display: flex;
    align-items: center;
    justify-content: center;
}

.display {
    background-color: #2e0f0f;
    box-shadow: 0 0 5px black;
    border-radius: 16px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    min-width: 80%;
    min-height: 80%;
    padding: 1rem;
}
.display-content {
    background: white;
    color: #19172b;
    box-shadow: 0 0 5px black;
    border-radius: 16px;
    text-align: center;
    padding: 1rem;
}