@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700&display=swap');

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: "Poppins", sans-serif;
}

.Container {
    color: black;
    background-color: #4f1919;
    height: 100vh;
    width: 100vw;
    border-radius: 0px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.True-page {
    background-color: #2e0f0f;
    box-shadow: 0 0 5px black;
    border-radius: 16px;
    display: flex;
    align-items: center;
    justify-content: center;
    min-width: 80%;
    min-height: 80%;
    padding: 1rem;
}

.About-me {
    background: white;
    color: #19172b;
    box-shadow: 0 0 5px black;
    border-radius: 16px;
    min-width: 50%;
    max-width: 80%;
    text-align: center;
    padding: 1rem;
}

.About-me img[id='Raponi'] {
    max-height: 10rem;
    margin-top: 0.2rem;
    border-radius: 16px;
    background-color: #19172b;
    padding: 0.3rem;
}

.Middle-page img[id='Insta'] {
    max-height: 3rem;
    margin-top: 0.2rem;
    border-radius: 16px;
}

.Middle-page img[id='GitHub'] {
    max-height: 3rem;
    min-height: 1rem;
    margin-left: 1rem;
    border-radius: 16px;
}

.Middle-page img[id='Youtube'] {
    max-height: 3rem;
    min-height: 1rem;
    margin-left: 1rem;
    border-radius: 16px;
}

.Projects p {
    margin-top: 1rem;
    font-size: 1.3em;
}